body {
  background-image: url("backgroundCat.2d4fe381.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.breed-select {
  max-width: 300px;
  width: 100%;
  height: 30px;
  transition: transform 250ms var(--timing-function);
  background-color: #fbedfa;
  border-radius: 4px;
  margin: 20px auto 20px 150px;
  overflow: auto;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 1px rgba(255, 3, 3, .14), 0 1px 3px rgba(176, 2, 2, .57), 0 2px 1px rgba(176, 44, 44, .647);
}

.breed-select > option {
  color: #891181;
}

.is-hidden {
  display: none;
}

.cat-info {
  text-align: center;
  max-width: 800px;
  height: auto;
  background-color: var(--secondary-light-color);
  border-radius: 0 0 4px 4px;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  display: flex;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

.box > h2, h3 {
  color: rgba(174, 18, 18, .973);
}

.loader {
  letter-spacing: .02em;
  color: #891181;
  font-size: 36px;
  font-weight: 700;
  line-height: 1.11;
}

.css-loader {
  letter-spacing: 2px;
  color: #ce13b2;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  font-weight: 600;
  animation: spotlight 2s linear infinite alternate;
  display: inline-block;
}

@keyframes spotlight {
  0%, 20% {
    opacity: 1;
    letter-spacing: 2px;
  }

  80%, 100% {
    opacity: 0;
    letter-spacing: 32px;
  }
}

